import { defineComponent, h, PropType  } from 'vue'
import { Doughnut } from 'vue-chartjs'

import {
  Chart as ChartJS,
  Title,
  Tooltip,
  Legend,
  ArcElement,
  CategoryScale,
} from 'chart.js'

ChartJS.register(
  Title, 
  Tooltip, 
  Legend, 
  ArcElement, 
  CategoryScale
)

export default defineComponent({
  name: 'DoughnutChart',
  components: {
    Doughnut
  },
  setup(props) {
    const chartData = {
      labels: [
        '', 
        '', 
        '', 
        ' ', 
        '', 
        ' ', 
        ' ',
        '',
        '',
        '',
        '',
      ],
      datasets: [
        {
          label: '  ',
          backgroundColor: ['#9FD72A','#FFA800','#59CFE9','#B67FBF','#CA985E','#F6DD00','#3E9B96','#5E8CFF','#FF7D7D','#088AA6','#475674'],
          data: [2000, 1800, 1500, 1600, 1000, 1200, 680, 820, 760, 758, 100],
          
        },
      ],
    }

    const chartOptions = {
      responsive: true,
      maintainAspectRatio: false,
      plugins:{
        legend: {
          display: false
        },
        
      },
      
      scales:{
        x:{
          display:false,
          grid:{
            display:false
          }
        },
        y:{
          display:false,
          grid:{
            display:false
          }
        },
      },
      cutout: '70',
      borderWidth:0
    }
    
    return () =>
      h(Doughnut, {
        chartData,
        chartOptions
      })  
  }
})