<template>
    <div style="padding:15vh">
        <div style="font-size:4vh;">   
          <div style="font-size:2vh; font-weight:normal; margin-top:4vh">         .<br>
                             .
          </div>

            <section>
                <xlsx-workbook>
                  <xlsx-sheet
                      :collection="sheet.data"
                      v-for="sheet in sheets"
                      :key="sheet.name"
                      :sheet-name="sheet.name"
                  />
                  <xlsx-download>
                      <button class="excel-down-btn">  </button>
                  </xlsx-download>
                </xlsx-workbook>
            </section>

            <div style="border:1px solid #D4D4D4; padding:3vh 2vw 2vw 5vh; margin-top:5vh; border-radius: 1vh;">
              <span style="font-size:2vh; font-weight:bold; margin-right:2vw">   </span>
              <input type="file" @change="onChange" />
              <xlsx-read :file="file">
                <xlsx-sheets>
                  <template #default="{sheets}">
                    <select v-model="selectedSheet">
                      <option v-for="sheet in sheets" :key="sheet" :value="sheet">
                        {{ sheet }}
                      </option>
                    </select>
                  </template>
                </xlsx-sheets>
                <xlsx-json :sheet="selectedSheet">
                  <template #default="{collection}">
                    <button class ="clickbtn" style="float:right; margin-top:5vh" @click="registerExcel(collection)"> </button>
                  </template>
                </xlsx-json>
              </xlsx-read>
            </div>
            
        </div>
      


      
    </div>
  </template>
  
  <script>
  import { XlsxRead, XlsxTable, XlsxSheets, XlsxJson, XlsxWorkbook, XlsxSheet, XlsxDownload } from "/home/hyemi/carbon/co2-emission-management/vueProject/node_modules/vue3-xlsx"
  import {useStore} from 'vuex'
  import {ref} from 'vue'
  export default {
    components: {
      XlsxRead,
      XlsxTable,
      XlsxSheets,
      XlsxJson,
      XlsxWorkbook,
      XlsxSheet,
      XlsxDownload
    },

    setup(){
      var file= ref(null)
      var selectedSheet=ref(null)
      var sheetName=ref(null)
      var sheets=ref([
        { name: "", 
          data: [
            { : String, : Date, :Date, :null, :null,:null,:Number}
          ] 
        }
      ])
      var collection=ref([{a:1, b:2}])
      var store = useStore()

      function onChange(event){
        file.value = event.target.files ? event.target.files[0] : null;
      }

      function addSheet(){
        sheets.push({ name: this.sheetName, data: [...this.collection] });
        sheetName.value = '';
      }
      function registerExcel(sheets){
        var i
        for(i=0; i<sheets.length; i++){
          console.log(i)
          var info_list={
            Type:"7",
            DetailType:"",
            StartDate: excelSerialDateToJSDate(sheets[i].),
            EndDate:excelSerialDateToJSDate(sheets[i].),
            Location:"",
            scope:2,
            data:sheets[i].,
            emissions:sheets[i].+4,
            Carbonunit:"kg",
            CarbonActivity:sheets[i].,
            kind:"",
            Division:{:"",:"",:""}
          }
          console.log(info_list)
          
          store.commit("SetTableContent",info_list)
        }
      }
      
      //      
      function formatDate(date) {
        var d = new Date(date),
            month = '' + (d.getMonth() + 1),
            day = '' + d.getDate(),
            year = d.getFullYear();

        if (month.length < 2) 
            month = '0' + month;
        if (day.length < 2) 
            day = '0' + day;

        return [year, month, day].join('-');
      }
      function excelSerialDateToJSDate (excelSerialDate) {
        const daysBeforeUnixEpoch = 70 * 365 + 19;
        const hour = 60 * 60 * 1000;
        return formatDate(new Date(Math.round((excelSerialDate - daysBeforeUnixEpoch) * 24 * hour) + 12 * hour));
      };
      

      return{
        file,
        selectedSheet,
        sheetName,
        sheets,
        collection,
        onChange,
        addSheet,
        registerExcel
      }
    },
    methods: {

      
    }
  };
  </script>

<style>
  .excel-down-btn{
    margin-top:3vh; 
    background: none; 
    border:none; 
    font-size:2vh; 
    color:#4FB0DA;
    text-decoration: underline;
  }
  .excel-down-btn:hover{
    cursor: pointer;
  }
</style>